.property-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 20px;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 90px;
    overflow: visible;
}
@media only screen and (min-width: 0) and (max-width: 800px) {
    .property-list {
        margin-bottom: 91px;
        margin-top: 75px;
    }
}